import {ValidationResult} from './interfaces/validation-result';
import {pdsI18n} from './pdsI18n';
export let numberValidationRules = {
  validateIsNumber: function validateIsNumber(input: string, condition: any, context: any): ValidationResult {
    //console.log('validating isNumber');
    let result: ValidationResult = {
      isValid: pdsI18n.isValidNumber(input)
    };
    if (result.isValid) {
      context.parsedNumber = pdsI18n.deLocalizeNumber(input);
    } else {
      result.errorMessage = 'Must be a number';
    }
    //console.log('result: ' + result.isValid);
    return result;
  },
  validateIsInteger: function validateIsInteger(input: string, condition: any, context: any): ValidationResult {
    // an integer string should have no chars that aren't numeric, except an optional negative sign at start
    //console.log('validating integer');
    let result: ValidationResult = {
      isValid: /^([-]?[0-9]*)?$/.test(input)
    };
    if (!result.isValid) {
      result.errorMessage = `${input} is not a valid integer.`;
    }
    //console.log(`result: ${result.isValid}`);
    return result;
  },
  validateMaxNumberValue: function validateMaxNumberValue(input: string, maxValue: number, context: any): ValidationResult {
    //console.log('validating max number');
    let result: ValidationResult = {
      isValid: context.parsedNumber <= maxValue
    };

    if (!result.isValid) {
      result.errorMessage = `The number must be less than or equal to ${maxValue}`;
    }
    //console.log('result: ' + result.isValid);
    return result;
  },
  validateMinNumberValue: function validateMinNumberValue(input: string, minValue: number, context: any): ValidationResult {
    //console.log('validating min number');
    let result: ValidationResult = {
      isValid: context.parsedNumber >= minValue
    };

    if (!result.isValid) {
      result.errorMessage = `The number must be greater than or equal to ${minValue}`;
    }
    //console.log('result: ' + result.isValid);
    return result;
  },
  validateMaxNumberLength: function validateMaxNumberLength(input: string, maxLength: number, context: any): ValidationResult {
    let result: ValidationResult = {
      isValid: input.length <= maxLength
    };
    if (!result.isValid) {
      result.errorMessage = `The number must have ${maxLength} or less digits.`;
    }
    return result;
  },
  validateMinNumberLength: function validateMinNumberLength(input: string, minLength: number, context: any): ValidationResult {
    let result: ValidationResult = {
      isValid: input.length >= minLength
    };
    if (!result.isValid) {
      result.errorMessage = `The number must have ${minLength} or more digits.`;
    }
    return result;
  },
  validateMaxNumberValueExclusive: function validateMaxNumberValueExclusive(input: string, maxValue: number, context: any): ValidationResult {
    //console.log('validating min number');
    let result: ValidationResult = {
      isValid: context.parsedNumber < maxValue
    };

    if (!result.isValid) {
      result.errorMessage = `The number must be less than ${maxValue}`;
    }
    //console.log('result: ' + result.isValid);
    return result;
  },
  validateMinNumberValueExclusive: function validateMinNumberValueExclusive(input: string, minValue: number, context: any): ValidationResult {
    //console.log('validating min number');
    let result: ValidationResult = {
      isValid: context.parsedNumber > minValue
    };

    if (!result.isValid) {
      result.errorMessage = `The number must be greater than ${minValue}`;
    }
    //console.log('result: ' + result.isValid);
    return result;
  },
  validateMaxDecimals: function validateMaxDecimals(input: string, maxDecimals: number, context: any): ValidationResult {
    let countDecimals = function(value): number {
      if ((value % 1) === 0) {
        //integer
        return 0;
      }
      return value.toString().split('.')[1].length;
    };
    //console.log('validating max decimals');
    let result: ValidationResult = {
      isValid: countDecimals(context.parsedNumber) <= maxDecimals
    };

    if (!result.isValid) {
      result.errorMessage = `The number cannot have exceed ${maxDecimals} decimal places`;
    }
    //console.log('result: ' + result.isValid);
    return result;
  }
};



