import {PdsWidget} from './PdsWidget';
import {NavItem} from './interfaces/nav-item';
export class PdsAppNav  extends PdsWidget {
  is = 'pds-app-nav';

  //Input
  navigation: Array<NavItem> = [];

  //Input
  homeNavItem: NavItem;

  //Input
  userNavigation: Array<NavItem>;

  //Input
  appName: string;

  //Input
  user: { firstName: string, lastName: string };

  // we are using null rather than undefined because Polymer doesn't update the DOM on undefined
  selectedNav: NavItem | null;

  // we are using null rather than undefined because Polymer doesn't update the DOM on undefined
  selectedSecondaryNav: NavItem | null;

  //In hamburger mode, the nav gets opened manually.
  navOpen: boolean = false;

  //When the user clicks their name, the user menu pops up
  userMenuOpen: boolean = false;

  //In desktop mode, the nav can be collapsed to make more room
  navCollapsed: boolean = false;

  //When clicking a primary item with children, the secondary menu opens up.
  //'hard' open it will be showing on the screen.
  //'soft' open means it will disappear at various responsive points.
  //'closed' means it will not be showing.
  private secondaryOpen: string = 'closed'; //'closed', 'hard', and 'soft' are the options.

  attached(): void {
    this.navCollapsed = false;
    if (!this.hasOwnProperty('appName')) {
      this.appName = 'PowerSchool';
    }

    setTimeout(() => {
      this.setHighlights();
    });
  }

  logoClickHandler(event: Event): void {
    event.preventDefault();

    this.closeSecondary(event);

    if (this.homeNavItem != null && this.homeNavItem.onUserClick != null) {
      this.homeNavItem.onUserClick(this.homeNavItem, event);
      setTimeout(() => {
        this.setHighlights();
      });
    }
  }

  primaryClickHandler(event: Event): void {
    event.preventDefault();

    let target = <HTMLElement & {dataNav: NavItem, dataIndex: string}>event.target;
    if (!this.pds.utils.is(event.target, 'a')) {
      target = <HTMLElement & {dataNav: NavItem, dataIndex: string}>this.pds.utils.getClosest(target, 'a');
    }

    let nav: NavItem = target.dataNav;
    this.selectedNav = nav;

    if (nav.children != null && nav.children.length > 0) {
      //This is how you tell polymer that something in an array changed.
      //this.notifyPath(`navigation.${target.dataIndex}.isActive`);

      this.setSecondaryOpenState('hard');
      // TODO: not sure if we want to set focus somewhere when the user opens the menu
      // setTimeout(this.setSecondaryFocus.bind(this));
    } else {
      this.closeNav();
      this.setSecondaryOpenState('closed');
      if (nav.onUserClick != null) {
        nav.onUserClick(nav, event);
      } else {
        throw 'NavItems that do not have children must have the onUserClick function';
      }
    }
  }

  secondaryClickHandler(event: Event): void {
    event.preventDefault();

    let target = <HTMLElement & {dataNav: NavItem, dataParent: NavItem}>event.target;
    if (!this.pds.utils.is(target, 'a')) {
      target = <HTMLElement & {dataNav: NavItem, dataParent: NavItem}>this.pds.utils.getClosest(target, 'a');
    }

    let nav: NavItem = target.dataNav;
    let parent: NavItem = target.dataParent;

    this.selectedSecondaryNav = nav;

    if (nav.onUserClick != null) {
      nav.onUserClick(nav, event);
    } else {
      throw `NavItem ${nav.id} did not have onUserClick function defined.`;
    }

    this.setSecondaryOpenState(this.navCollapsed ? 'closed' : 'soft');
    this.closeNav();
  }

  userMenuClickHandler(event: Event): void {
    event.preventDefault();

    let target = <HTMLElement & {dataNav: NavItem}>event.target;
    if (!this.pds.utils.is(target, 'a')) {
      target = <HTMLElement & {dataNav: NavItem}>this.pds.utils.getClosest(target, 'a');
    }

    let nav = target.dataNav;

    this.toggleUserMenu();
    this.closeNav();

    if (nav.onUserClick != null) {
      nav.onUserClick(nav, event);
    } else {
      throw `NavItem ${nav.id} did not have onUserClick function defined.`;
    }

    this.setSecondaryOpenState('closed');
  }

  closeSecondary(event: Event): void {
    event.preventDefault();
    this.setSecondaryOpenState('closed');
    // TODO: not sure if we want to set focus somewhere when the user closes the menu
    // setTimeout(this.setPrimaryFocus.bind(this));
    this.setHighlights('closed');
  }

  //Hamburger menu triggers a class state change on the component level for styling
  toggleNav(event: Event): void {
    event.preventDefault();
    this.navOpen = !this.navOpen;
  }

  closeNav(): void {
    this.navOpen = false;
  }

  toggleUserMenu(event?: Event): void {
    if (event != null) {
      event.preventDefault();
    }
    this.userMenuOpen = !this.userMenuOpen;

  }

  toggleNavCollapsed(event: Event): void {
    event.preventDefault();
    this.navCollapsed = !this.navCollapsed;

    //pds-is-collapsed is a class for the layout of the page to use.
    //pds-is-nav-collapsed is a class for the internal widget to style off of.
    if (this.navCollapsed) {
      this.classList.add('pds-is-collapsed');
      this.setSecondaryOpenState('closed');
    } else {
      this.classList.remove('pds-is-collapsed');
    }

    //We fire an event here for things that use navigation
    //but are not using PDS for layout.  Namely: PTP.
    this.fire('pdsToggleNav', {navCollapsed: this.navCollapsed});
  }

  messageKeysUpdated(): void {}

  private setSecondaryOpenState(secondaryOpen: string): void {
    this.secondaryOpen = secondaryOpen;
  }

  private setHighlights(secondaryState = 'soft'): void {
    this.selectedNav = null;
    this.selectedSecondaryNav = null;

    (<any>this.navigation).find((navItem: NavItem) => {
      //When there are children, search through them to see if one is active, if so, highlight the parent too.
      if (navItem.children != null && navItem.children.length > 0) {
        return null != (<any>navItem.children).find((childItem: NavItem) => {
          if (childItem.isCurrentRoute != null && childItem.isCurrentRoute(childItem)) {
            this.selectedSecondaryNav = childItem;
            this.selectedNav = navItem;
            this.setSecondaryOpenState(secondaryState);
            return true;
          }
          return false;
        });
      }

      //If no children, then see if the item is active
      if (navItem.isCurrentRoute != null && navItem.isCurrentRoute(navItem)) {
        this.selectedNav = navItem;
        return true;
      }

      //Nothing found, move on to the next one.
      return false;
    });
  }

  private isSecondaryOverlay(): boolean {
    let windowWidth = window.innerWidth;
    return !((windowWidth > 1024 && !this.navCollapsed) || windowWidth <= 768);
  }
}



