import {PdsWidget} from './PdsWidget';
let isIntegerRegex = /^([-]?[0-9]*)?$/;

export class PdsProgressBarWidget extends PdsWidget {
  is = 'pds-progress-bar-widget';

  width: number;

  properties = {
    width: {
        type: Number,
        value: 0,
        notify: true,
        reflectToAttribute: true
      }
  };

  messageKeysUpdated(): void {

  }

  validateWidget(): boolean {
    return true;
  };
}



