import {PdsWidget} from './PdsWidget';

export class PdsImg extends PdsWidget {
  is = 'pds-img';

  src: string;
  fallbackIconName: string;
  useImage: boolean;
  alt: string;
  width: number;
  height: number;

  // need properties to bind attributes without javascript
  properties = {
    src: String,
    fallbackIconName: String,
    alt: String,
    width: Number,
    height: Number
  };

  messageKeysUpdated(): void {
    //must override
  }

  ready(): void {
    //set default values
    this.useImage = true;
    this.alt = 'PowerSchool image';
  }

  onImageError(): void {
    //this is called when src is invalid
    this.useImage = false;
  }

  attached(): void {
    if (this.fallbackIconName == null || this.fallbackIconName.length === 0) {
      throw 'Must configure a fallBackIconName';
    }
  }
}



