import {PdsAbstractInputWidget} from './pds-abstract-input-widget';
import {BasicValidationRules} from './interfaces/basic-validation-rules';
let isIntegerRegex = /^([-]?[0-9]*)?$/;

export class PdsNumberWidget extends PdsAbstractInputWidget {
  is = 'pds-number-widget';

  //Validation Properties
  dataIsinteger: string;
  dataMinlength: string;
  dataMaxlength: string;
  dataMaxvalue: string;
  dataMinvalue: string;
  dataMaxdecimals: string;
  dataMinvalue_exclusive: string;
  dataMaxvalue_exclusive: string;

  properties = {
    //AbstractInputWidget Properties
    name: String, //We construct the id using the name property
    dataLabelText: String,
    dataBadgeText: {
      type: String,
      value: '',
      observer: 'setUpBadge'
    },
    dataHelperText: {
      type: String,
      value: null
    },
    dataFieldHelpText: String,
    dataPlaceholderText: String,
    dataTooltipText: String,
    dataRegexErrorText: String,
    dataIsreadonly: {
      type: String,
      value: 'false',
      observer: 'setReadonlyText'
    },
    dataIsfieldonly: {
      //This is for adding a class to get rid of the bottom padding.
      type: String,
      value: 'false'
    },
    dataHideUnelevatedBadge: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    dataDisableInvalidBadgeElevation: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    //Widget specific Validation Properties
    dataIsrequired: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    dataIsinteger: String,
    dataMinlength: String,
    dataMaxlength: String,
    dataMaxvalue: String,
    dataMinvalue: String,
    dataMaxdecimals: String,
    dataMinvalue_exclusive: String,
    dataMaxvalue_exclusive: String,
    dataCustomvalidate: String
  };

  generateValidationRules(): void {
    let validationRules: BasicValidationRules = this.pds.validationEngine.getBasicValidationRules();
    validationRules.isNumber = true;
    validationRules.setProperty('isRequired', this.dataIsrequired, Boolean);
    validationRules.setProperty('isInteger', this.dataIsinteger, Boolean);
    validationRules.setProperty('minLength', this.dataMinlength, Number);
    validationRules.setProperty('maxLength', this.dataMaxlength, Number);
    validationRules.setProperty('minValue', this.dataMinvalue, Number);
    validationRules.setProperty('maxValue', this.dataMaxvalue, Number);
    validationRules.setProperty('minValueExclusive', this.dataMinvalue_exclusive, Number);
    validationRules.setProperty('maxValueExclusive', this.dataMaxvalue_exclusive, Number);
    validationRules.setProperty('maxDecimals', this.dataMaxdecimals, Number);

    this.validators = this.pds.validationEngine.getValidatorsForRules(validationRules);

    if (this.hasOwnProperty('dataCustomvalidate')) {
      this.validators.push({
        execute: this.pds.validation.getCustomValidate(this.dataCustomvalidate)
      });
    }
  }

  /**
   * @param modelValue
   * @param skip
   * @returns returns if the model value was actually set.
   */
  setModelValue(modelValue: number, skip: boolean): boolean {
    //TODO - Figure out how to tell typescript that I can access querySelector off of this.
    let ele = this.querySelector('input');
    if (ele != null && modelValue !== ele.modelValue) {
      ele.modelValue = modelValue;
      if (skip !== true) {
        this.setValue(this.pds.i18n.localizeNumber(modelValue), true);
      }
      return true;
    }
    return false;
  };

  messageKeysUpdated(): void {

  }

  /**
   *
   * @param value - The string the user typed into the input.
   * @param skip - skips setting the model value. (Keeps from getting into recursive loops)
   */
  setValue(value: string, skip = false): void {
    if (value == null) {
      value = '';
    }

    let inputEl = this.querySelector('input');
    if (inputEl != null && value !== inputEl.value) {
      inputEl.value = value;
    }

    if (skip !== true) {
      this.setModelValue(this.pds.i18n.deLocalizeNumber(value), true);
    }
  }
}



