import {ValidationResult} from './interfaces/validation-result';
import {Validator} from './interfaces/validator';
export let pdsValidation = {
  pds: undefined,
  _customValidationFunctions: {},
  /**
   * Searches the DOM element for all descendants that are widgets that can be validated
   * and runs their validation functions which will put any error messages up on the screen
   * as well as return whether all elements are valid within the container or not.
   * @param element: a DOM element
   * @returns true if all the elements are valid
   *          false if one or more elements are NOT valid
   */
  validateElements: function (element: HTMLElement): boolean {
    if (element == null) {
      return true;
    }

    let allElementsAreValid = true;

    let inputWidgets = element.querySelectorAll('pds-number-widget,pds-date-widget,pds-text-widget');
    for (let i = 0; i < inputWidgets.length; i++) {
      //It's important that the AND allElementsAreValid is tested AFTER numWidgets[i].validateNumber
      //so ALL elements will be validated.
      //The parameter to validateWidget here could come from parameters of this method to enable us
      //to tell the widget whether or not it should show errors after validation.
      allElementsAreValid = (<any>inputWidgets[i]).validateWidget(true) && allElementsAreValid;
    }

    return allElementsAreValid;
  },
  /**
   * Registers a function to be used as a validator for validation widgets.
   * @param functionName: the key by which we will look up this function. See getCustomValidate for more info.
   * @param executeFunction: the function that will do the validation. Must meet the following interface:
   * (value: string, condition: any, context: {[key: string]: any}) => ValidationResult
   */
  registerCustomValidate: function(functionName: string, executeFunction: (value: string, condition: any, context: {[key: string]: any}) => ValidationResult): void {
    if (functionName == null || functionName === '' || executeFunction == null) {
      return;
    }
    this._customValidationFunctions[functionName] = executeFunction;
  },
  /**
   * Looks up a validator function that was previously registered by registerCustomValidate. This validator is used
   * by validation widgets. Being able to implement custom validators adds flexibility to the widgets.
   * @param functionName: the name used to register the validator in registerCustomValidate
   * @returns {any}: a function used as a validator.
   */
  getCustomValidate: function(functionName: string): (value: string, condition: any, context: {[key: string]: any}) => ValidationResult {
    return this._customValidationFunctions[functionName];
  }
};



