import {PdsWidget} from './PdsWidget';
import {StudentSearchResult} from './interfaces/search-result';

export class PdsSearch extends PdsWidget {
  is = 'pds-search';

  searchQuery: string;

  students: Array<StudentSearchResult>;

  attached(): void {
    document.body.addEventListener('click', () => {
      this.searchQuery = '';
    });
  }

  messageKeysUpdated(): void {}

  iconClickHandler(): void {
    this.querySelector('input').focus();
  }

  inputChange(event: Event): void {
    this.searchQuery = (<any>event.target).value;

    this.debounce('pdsInputChangeFire', function(): void {
      this.fire('pdsInputChange', this.searchQuery.trim());
    }, 250);
  }

  getInputClass(searchQuery): string {
    return searchQuery != null && searchQuery.trim().length > 0 ? 'expanded' : '';
  }

  isSearchQueryOverLimit(searchQuery): boolean {
    return searchQuery != null && searchQuery.trim().length > 1;
  }

  studentClickHandler(event: Event): void {
    event.preventDefault();
    event.stopPropagation();

    let target = <HTMLElement & {dataStudent: StudentSearchResult}>event.target;
    if (!this.pds.utils.is(target, 'a')) {
      target = <HTMLElement & {dataStudent: StudentSearchResult}>this.pds.utils.getClosest(target, 'a');
    }

    let student: any = target.dataStudent;

    this.fire('pdsStudentClick', student.uid);

    this.searchQuery = '';
  }
}



