import {ValidationResult} from './interfaces/validation-result';
import {pdsI18n} from './pdsI18n';

export let dateValidationRules = {
  validateIsDate: function validateIsDate(input: string, condition: any, context: any): ValidationResult {
    //console.log('validating isDate');
    let result: ValidationResult = {
      isValid: pdsI18n.isValidDate(input)
    };
    if (result.isValid) {
      context.parsedDate = pdsI18n.deLocalizeDate(input);
    } else {
      result.errorMessage = 'Must be a date';
    }
    //console.log('result: ' + result.isValid);
    return result;
  },
  validateMaxDate: function validateMaxDate(input: string, maxDate: Date, context: any): ValidationResult {
    //console.log('validating maxDate');

    let result: ValidationResult = {
      isValid: (<Date>context.parsedDate).getTime() <= maxDate.getTime()
    };

    if (!result.isValid) {
      result.errorMessage = 'The date must be on or before ' + pdsI18n.localizeDate(maxDate);
    }
    //console.log('result: ' + result.isValid);
    return result;
  },
  validateMinDate: function validateMinDate(input: string, minDate: Date, context: any): ValidationResult {
    //console.log('validating minDate');

    let result: ValidationResult = {
      isValid: (<Date>context.parsedDate).getTime() >= minDate.getTime()
    };

    if (!result.isValid) {
      result.errorMessage = 'The date must be on or after ' + pdsI18n.localizeDate(minDate);
    }
    //console.log('result: ' + result.isValid);
    return result;
  }
};



