export abstract class PdsWidget {

  pds: any;

  //classList is an HTML element property.
  classList: any;

  //This is how you tell Polymer that something needs to run through change detection.
  notifyPath: (value: string) => void;

  // Polymer built in function to fire events
  fire: (string, any) => {};

  addEventListener: (string, any) => {};

  // Polymer built in function to debounce
  debounce: (string, Function, number) => {};

  /**
   * This will be called on all of the elements that are in the DOM when the message keys get updated.
   */
  abstract messageKeysUpdated(): void;

  ready(): void {
    this.addMessageKeyUpdatedClass();
  }

  addMessageKeyUpdatedClass(): void {
    if (!this.pds.i18n.isUpdatedMessageKeys) {
      (<any>this).classList.add('pds-default-messages');
    }
  }

  removeMessgeKeyUpdatedClass(): void {
    if (this.pds.i18n.isUpdatedMessageKeys) {
      (<any>this).classList.remove('pds-default-messages');
    }
  }

  /* The following are functions that get attached as DOM element functions. */
  querySelector(param: string): any {
    //to be overridden by an overriding element.
    console.error('this should never have been called');
    //NOTE: It should be deleted in pdsMain and then inherited from the DOM element.
  }

  dispatchEvent(e: Event): void {
    //to be overridden by an overriding element.
    console.error('this should never have been called');
    //NOTE: It should be deleted in pdsMain and then inherited from the DOM element.
  }

  hasAttribute(name: string): boolean {
    console.error('this should never have been called');
    return false;
  }

  /* The following are functions made to make Polymer template syntax suck less*/
  logicalAnd(...value: Array<boolean | undefined>): boolean {

    let answer = true === value.reduce((v1: boolean | undefined, v2: boolean | undefined) => {
      return v1 && v2;
    });
    //console.log('Anding: ' + value[0] + ' ' + value[1] + ' answer: ' + answer);
    return answer;
  }

  /**
   * Created so we don't have to create so many getClass functions.
   * Examples:
   * {{conditionClass('pds-is-active', isShowing)}}
   * {{conditionClass('pds-is-active', user, '!= null')}}
   * {{conditionClass('pds-is-active', myAge, '<', yourAge)}}
   */
  conditionalClass(className: string, value1: any, operator?: string, value2?: any): string | undefined {
    if (operator == null) {
      if (value1) {
        return className;
      }
    } else if (this.compare(value1, operator, value2)) {
      return className;
    }
  }

  compare(value1: any, operator: string, value2?: any): boolean {
    switch (operator.trim()) {
      case 'is-blank':
        return value1 == null || value1.length === 0;
      case '== null':
      case '==null':
        return value1 == null;
      case '!= null':
      case '!=null':
        return value1 != null;
      /* tslint:disable */
      case '==':
        return value1 == value2;
      case '!=':
        return value1 != value2;
      /* tslint:enable */
      case '===':
        return value1 === value2;
      case '!==':
        return value1 !== value2;
      case '<':
        return value1 < value2;
      case '<=':
        return value1 <= value2;
      case '>':
        return value1 > value2;
      case '>=':
        return value1 >= value2;
      default:
        throw 'operator not found';
    }
  }
}



