import {PdsWidget} from './PdsWidget';
import {pdsUtils} from './pdsUtils';
import {Validator} from './interfaces/validator';

export abstract class PdsAbstractInputWidget extends PdsWidget {

  name: string;

  errorMessage: string;
  isValid: boolean;
  invalidRule: string;
  isAttached: boolean;
  isBadgeElevated: boolean;

  //Validation rules
  dataIsreadonly: string;
  dataIsrequired: string;
  dataCustomvalidate: string;

  //Validation Options
  dataHideUnelevatedBadge: string;
  dataDisableInvalidBadgeElevation: string;

  //Visibility Flags
  isBadgeHidden: boolean;
  showTooltip: boolean;
  canShowError: boolean;   //this is set to true if validateWidget is called with a param of true and we are in an error state
                            //right now the only things that make that happen are focusLost and pds.validation.validateElements
  //Text labels
  dataLabelText: string;
  dataBadgeText: string;
  dataHelperText: string;
  dataFieldHelpText: string;
  dataPlaceholderText: string;
  dataTooltipText: string;

  originalBadgeText: string;

  validators: Array<Validator>;

  attributeChanged(): void {
    //this is a polymer hook
    this.generateValidationRules();
  }

  ready(): void {     //this is a polymer hook
    //Initialize all of the member variables here because it doesn't
    //work right to do that in the place where the variables are defined.
    this.errorMessage = '';
    this.isValid = true;
    this.invalidRule = '';
    this.isAttached = false;
    this.isBadgeElevated = false;

    this.isBadgeHidden = true;
    this.showTooltip = false;
    this.canShowError = false;

    this.originalBadgeText = '';

    this.messageKeysUpdated();
    this.generateValidationRules();
  }

  inputChange(): void {
    this.validateWidget();
    this.setValue(this.value);
    (<any>this).dispatchEvent(new Event('modelValueChange'));
  }

  focusLost(): void {
    this.validateWidget(true);
  }

  get value(): any {
    let result = this.querySelector('input');
    if (result != null) {
      return result.value;
    }
  }

  set value(value) {
    this.setValue(value);
    this.setReadonlyText();
  }

  get modelValue(): any {
    let result = this.querySelector('input');

    if (result != null) {
      //In the Angular 2+ event listener, we grab event.target.modelValue
      return result.modelValue;
    }
  }

  set modelValue(value) {
    let modelValueChanged = this.setModelValue(value);
    if (modelValueChanged) {
      this.validateWidget();
    }
    this.setReadonlyText();
  }

  validateWidget(showError = false): boolean {
    let validationResult = this.pds.validationEngine.validate(this.value, this.validators);
    this.isValid = validationResult.isValid;

    if (validationResult.isValid) {
      this.setError('');
      this.invalidRule = '';
      this.setUpBadge();
      return true;
    } else {
      this.invalidRule = validationResult.invalidRule;
      this.setUpBadge();
      if (validationResult.invalidRule === 'isRequired') {
        this.setError('');
      } else {
        this.setError(validationResult.errorMessage);
      }
      // this if statement keeps the error from showing until focusLost or pds.validation.validateElements is called
      if (showError) {
        this.canShowError = true;
      }

      return false;
    }
  }

  setError(errorMsg): void {
    if (errorMsg != null && errorMsg.trim() !== '') {
      this.errorMessage = errorMsg;
    } else {
      this.errorMessage = '';
    }
  }

  abstract setValue(value: any, skip?: boolean): void;

  abstract setModelValue(value: any, skip?: boolean): void;

  abstract generateValidationRules(): void;

  attached(): void {  //this is a polymer hook
    let valueAttr = (<any>this).getAttribute('value');
    if (valueAttr != null) {
      (<any>this).removeAttribute('value');
      this.value = valueAttr;
    }

    this.isAttached = true;

    //Initialize the badge
    this.setUpBadge();
  }

  setReadonlyText(): void {
    if (this.dataIsreadonly != null && this.value != null) {
      let readOnlyEle = this.querySelector('.pds-readonly-data');
      if (readOnlyEle == null) {
        return;
      }

      if (this.dataIsreadonly === 'true') {
        readOnlyEle.innerText = this.value;
      } else {
        readOnlyEle.innerText = '';
      }
    }
  }

  setUpBadge(): void {
    if (this.isAttached) {  // if attached hasn't fired yet this won't work
      //calculate parameters
      let isElevated = !this.isValid && this.dataDisableInvalidBadgeElevation !== 'true'
        && this.invalidRule === 'isRequired' && this.dataIsrequired === 'true';
      let isBadgeTextConfigured = (this.hasAttribute('data-badge-text')
      && this.dataBadgeText != null && this.dataBadgeText.length > 0);
      let hidingUnelevated = this.dataHideUnelevatedBadge === 'true' && !isElevated;
      let isRequired = this.dataIsrequired === 'true';

      //set badge elevation
      this.isBadgeElevated = isElevated;

      //set original badge text
      if (this.dataBadgeText != null && this.dataBadgeText != null
        && this.dataBadgeText.length > 0 && this.originalBadgeText === '') {
        this.originalBadgeText = this.dataBadgeText;
      }

      //set badge text
      if (this.dataIsrequired === 'true') {
        this.dataBadgeText = this.pds.i18n.getText(this.pds.i18n.widgetKeys['textWidget.required']);
      } else if (!isBadgeTextConfigured) {
        this.originalBadgeText = '';
      } else if (isBadgeTextConfigured) {
        this.dataBadgeText = this.originalBadgeText;
      }

      //set badge visibility
      if (!hidingUnelevated && (isRequired || isBadgeTextConfigured)) {
        this.isBadgeHidden = false;
      } else {
        this.isBadgeHidden = true;
      }
    }
  }

  showLabel(dataLabelText: string): boolean {
    return (dataLabelText != null && dataLabelText.length > 0);
  }

  showFieldHelp(dataFieldHelpText: string): boolean {
    return (this.dataTooltipText != null && this.dataTooltipText.length > 0);
  }

  showHelperText(dataHelperText: string): boolean {
    return (this.dataHelperText != null && this.dataHelperText.length > 0);
  }

  hideError(canShowError: boolean, isValid: boolean, errorMessage: string): string {
    if (canShowError && !isValid && errorMessage != null && errorMessage.length > 0) {
      return '';
    } else {
      return 'pds-is-hidden';
    }
  }
}



