import {PdsDateWidget} from './pds-date-widget';
import {PdsDateWidgetDialog} from './pds-date-widget-dialog';
import {PdsAppNav} from './pds-app-nav';
import {PdsClassBlock} from './pds-class-block';
import {PdsClassPicker} from './pds-class-picker';
import {PdsDynamicSelect} from './PdsDynamicSelect';
import {PdsTextWidget} from './pds-text-widget';
import {PdsNumberWidget} from './pds-number-widget';
import {pdsLogger} from './pdsLogger';
import {pdsI18n} from './pdsI18n';
import {pdsUtils} from './pdsUtils';
import {pdsValidation} from './pdsValidation';
import {PdsAppSwitcherWidget} from './PdsAppSwitcherWidget';
import {PdsNumberedSlider} from './PdsNumberedSlider';
import {PdsNaturalSort} from './PdsNaturalSort';
import {pdsValidationRulesEngine} from './pdsValidationRulesEngine';
import {PdsProgressBarWidget} from './PdsProgressBarWidget';
import {PdsReportingTermSelector} from './pds-reporting-term-selector';
import {PdsSearch} from './pds-search';
import {PdsIcon} from './PdsIcon';
import {PdsRichTextEditor} from './pds-rich-text-editor';
import {PdsImg} from './pds-img';

let powerSchoolDesignSystemToolkit;

let w: any = window;

if (w.powerSchoolDesignSystemToolkit == null) {
  /**
   * This function works just like Object.getOwnPropertyDescriptor but it gets the property descriptor
   * even if it's inherited.
   * @param object - The object to get the property off of.
   * @param prop - The property to get a descriptor for.
   * @returns {PropertyDescriptor} The property descriptor of the property.
   */
  let getPropertyDescriptor = function(object: any, prop: string): PropertyDescriptor {
    let objectDescriptor: PropertyDescriptor = Object.getOwnPropertyDescriptor(object, prop);
    if (objectDescriptor == null) {
      return getPropertyDescriptor(Object.getPrototypeOf(object), prop);
    } else {
      return objectDescriptor;
    }
  };

  /**
   * Polymer needs an object definition with all properties on the same object with no inheritance tree. It
   * also needs an actual object, not an object constructor. Using TypeScript to create Polymer definitions
   * is super convenient because it allows us to take advantage of inheritance, but it gives us back an object
   * that is a constructor, and has an inheritance tree.  This function takes the constructor generated by
   * typescript, and returns a new object instance that has all of the properties and functions all defined
   * as non-inherited properties of a new instance of the passed in constructor.
   */
  let createFlattenedPolymerDefinition = (token: any): any => {
    let tsObject = new token();
    let definition = {};
    let objectDef;
    /* tslint:disable */
    for (let prop in tsObject) {
      /* tslint:enable */
      objectDef = getPropertyDescriptor(tsObject, prop);
      if (objectDef != null && objectDef.hasOwnProperty('get')) {
        Object.defineProperty(definition, prop, objectDef);
      } else {
        definition[prop] = tsObject[prop];
      }
    }

    delete (<{ querySelector: any }>definition).querySelector;
    delete (<{ dispatchEvent: any }>definition).dispatchEvent;
    delete (<{ hasAttribute:  any }>definition).hasAttribute;
    return definition;
  };

  let pds = {
    i18n: pdsI18n,
    validation: pdsValidation,
    utils: pdsUtils,
    logger: pdsLogger,
    log: pdsLogger.log,
    naturalSort: new PdsNaturalSort,
    naturalSortConstructor: PdsNaturalSort,
    validationEngine: pdsValidationRulesEngine,
    setAutomationChecker: function(automationChecker: any): void {
      if (this.automationChecker == null) {
        this.automationChecker = automationChecker;
        if (localStorage.getItem('debug') === 'true') {
          this.automationChecker.startChecking();
        }
      } else {
        console.error('The automation checker is already being downloaded in this app. To turn it on run localStorage.setItem(\'debug\', \'true\' and reload the page');
      }
    },
    _pdsWidgetDefinitions: {
      pdsTextWidgetDefinition: createFlattenedPolymerDefinition(PdsTextWidget),
      pdsNumberWidgetDefinition: createFlattenedPolymerDefinition(PdsNumberWidget),
      pdsDateWidgetDefinition: createFlattenedPolymerDefinition(PdsDateWidget),
      pdsDateWidgetDialogDefinition: createFlattenedPolymerDefinition(PdsDateWidgetDialog),
      pdsDynamicSelectDefinition: createFlattenedPolymerDefinition(PdsDynamicSelect),
      pdsAppSwitcherDefinition: createFlattenedPolymerDefinition(PdsAppSwitcherWidget),
      pdsNumberedSliderDefinition: createFlattenedPolymerDefinition(PdsNumberedSlider),
      pdsIconDefinition: createFlattenedPolymerDefinition(PdsIcon),
      pdsAppNav: createFlattenedPolymerDefinition(PdsAppNav),
      pdsClassBlock: createFlattenedPolymerDefinition(PdsClassBlock),
      pdsClassPicker: createFlattenedPolymerDefinition(PdsClassPicker),
      pdsReportingTermSelector: createFlattenedPolymerDefinition(PdsReportingTermSelector),
      pdsSearch: createFlattenedPolymerDefinition(PdsSearch),
      pdsRichTextEditor: createFlattenedPolymerDefinition(PdsRichTextEditor),
      pdsProgressBarWidgetDefinition: createFlattenedPolymerDefinition(PdsProgressBarWidget),
      pdsImgWidgetDefinition: createFlattenedPolymerDefinition(PdsImg)
    },
    svgSpritePath: '../../../../img/icons/pds-icons.svg'
  };
  pds.naturalSort.ignoreCase();

  //give a pointer to the library from each of the sub modules.
  for (let prop in pds) {
    if (prop !== '_pdsWidgetDefinitions' && prop !== 'svgSpritePath') {
      pds[prop].pds = pds;
    }
  }

  for (let prop in pds._pdsWidgetDefinitions) {
    if (prop !== '_pdsWidgetDefinitions') {
      pds._pdsWidgetDefinitions[prop].pds = pds;
    }
  }

  //Add the library to the window so Polymer can read from it.
  if (w.powerSchoolDesignSystemToolkit == null) {
    w.powerSchoolDesignSystemToolkit = pds;
  }
}
powerSchoolDesignSystemToolkit = w.powerSchoolDesignSystemToolkit;
export default powerSchoolDesignSystemToolkit;



