import { PdsAbstractInputWidget } from './pds-abstract-input-widget';
import { BasicValidationRules } from './interfaces/basic-validation-rules';
import { basicOptions, advancedOptions, readOnlyOptions, noToolbarOptions} from './rich-text-editor-configurations';

declare let CKEDITOR;

export class PdsRichTextEditor extends PdsAbstractInputWidget {
  is = 'pds-rich-text-editor';
  editor: any;
  dataMaxlength: string;
  dataIsAdvanced: string;
  dataNoToolbar: string;
  ignoreEditorChange: boolean;

  properties = {
    name: {
      type: String,
      value: ''
    },
    dataBadgeText: {
      type: String,
      value: '',
      observer: 'setUpBadge'
    },
    dataHideUnelevatedBadge: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    dataDisableInvalidBadgeElevation: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    dataIsrequired: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    dataMaxlength: String,
    dataIsAdvanced: {
      type: String,
      observer: 'initializeEditor'
    },
    dataIsreadonly: String,
    dataNoToolbar: String
  };

  generateValidationRules(): void {
    let validationRules: BasicValidationRules = this.pds.validationEngine.getBasicValidationRules();

    validationRules.isRequired = true;
    validationRules.isText = true;
    validationRules.setProperty('maxLength', this.dataMaxlength, Number);

    this.validators = this.pds.validationEngine.getValidatorsForRules(validationRules);

    if (this.hasOwnProperty('dataCustomvalidate')) {
      this.validators.push({
        execute: this.pds.validation.getCustomValidate(this.dataCustomvalidate)
      });
    }
  }

  showBadge(dataBadgeText: string): boolean {
    return (dataBadgeText != null && dataBadgeText.length > 0);
  }

  getOptions(): any {
    let options: any;
    if (this.dataIsreadonly === 'true') {
      options = readOnlyOptions;
    } else if (this.dataIsAdvanced === 'true') {
      options = advancedOptions;
    } else if (this.dataNoToolbar === 'true') {
      options = noToolbarOptions;
    } else {
      options = basicOptions;
    }

    options.wordcount.maxCharCount = this.dataMaxlength;

    return options;
  }

  attached(): void {
    super.attached();
    this.initializeEditor();
  }

  initializeEditor(): void {
    if (this.editor != null) {
      let data = this.modelValue;
      this.editor.focusManager.blur(true);
      this.editor.destroy(true);
      this.editor = CKEDITOR.replace(this.name + '-rich-text-editor', this.getOptions());
      this.editor.setData(data);
    } else {
      this.editor = CKEDITOR.replace(this.name + '-rich-text-editor', this.getOptions());
    }

    this.editor.on('change', () => {
      if (!this.ignoreEditorChange) {
        this.inputChange();
      }
    });
  }

  messageKeysUpdated(): void {
  }

  get value(): any {
    if (this.editor != null) {
      return this.editor.getData();
    }
  }

  /**
   * Overridden because because this widget does not use an <input>.
   */
  get modelValue(): any {
    if (this.editor != null) {
      return this.editor.getData();
    }
  }

  /**
   * Identical to the super function, but copied here because deleting it resulted in a lot of errors.
   * I think there must have been an issue with inheritance and get/set pairs.
   */
  set modelValue(value) {
    /**
     * We need to ignore the synchronous 'change' event that the CKEDITOR will fire
     * because it will throw errors in Angular 2+. We only need to handle the change events
     * when the changes are coming directly from the CKEDITOR anyway.
     */
    this.ignoreEditorChange = true;
    let modelValueChanged = this.setModelValue(value);
    if (modelValueChanged) {
      this.validateWidget();
    }
    this.ignoreEditorChange = false;
  }

  /**
   *
   * @param value - The string the user typed into the input.
   * @param skip - skips setting the model value. (Keeps from getting into recursive loops)
   */
  setValue(value: string, skip = false): void {
    if (value == null) {
      value = '';
    }

    if (this.editor != null && value !== this.editor.getData()) {
      this.editor.setData(value);
    }

    if (skip !== true) {
      this.setModelValue(value, true);
    }
  }

  /**
   * @param modelValue
   * @param skip
   * @returns returns if the model value was actually set.
   */
  setModelValue(modelValue: string, skip?: boolean): boolean {
    if (this.editor != null && modelValue !== this.editor.getData()) {
      this.editor.setData(modelValue);
      if (skip !== true) {
        this.setValue(modelValue, true);
      }
      return true;
    }
    return false;
  }
}



