import {ValidationResult} from './interfaces/validation-result';
export let textValidationRules = {
  validateMaxTextLength: function validateMaxTextLength(input: string, maxLength: number, context: any): ValidationResult {
    let result: ValidationResult = {
      isValid: input.length <= maxLength
    };
    if (!result.isValid) {
      result.errorMessage = `The text must have ${maxLength} or less characters.`;
    }
    return result;
  },
  validateMinTextLength: function validateMinTextLength(input: string, minLength: number, context: any): ValidationResult {
    let result: ValidationResult = {
      isValid: input.length >= minLength
    };
    if (!result.isValid) {
      result.errorMessage = `The text must have ${minLength} or more characters.`;
    }
    return result;
  },
  validateExcludeChars: function validateExcludeChars(input: string, condition: {pattern: RegExp, excludeChars: string}, context: any): ValidationResult {
    //console.log('validating excludeChars');

    let result: ValidationResult = {
      isValid: !condition.pattern.test(input)
    };

    if (!result.isValid) {
      result.errorMessage = `Must not contain any characters in ${condition.excludeChars}`;
    }
    //console.log('result: ' + result.isValid);
    return result;
  },
  validateRegex: function validateRegex(input: string, condition: {pattern: RegExp, errorMessage: string}, context: any): ValidationResult {
    let result: ValidationResult = {
      isValid: condition.pattern.test(input)
    };

    if (!result.isValid) {
      result.errorMessage = condition.errorMessage;
    }
    return result;
  }
};



