import {PdsWidget} from './PdsWidget';
import {ClassPickerClass} from './interfaces/class-picker-class';
import {ClassSchedule} from './interfaces/class-schedule';
import {ClassPickerNavItem} from './interfaces/class-picker-nav-item';
import {pdsUtils} from './pdsUtils';
import {ClassPickerStudent} from './interfaces/class-picker-student';

declare let Polymer;

export class PdsClassPicker extends PdsWidget {
  is = 'pds-class-picker';

  selectedClass: ClassPickerClass;

  classSchedule: ClassSchedule;
  // used to list the students in which the current user is looking at classes for (parent use case)
  studentOptions: Array<ClassPickerStudent>;
  // used for quick lookup of student data so that we can filter the sections
  studentMap: Map<string, ClassPickerStudent>;
  // the selected student's uid
  selectedStudent: string;

  selectedYear: string;

  years: Array<string>;

  possibleYears: Array<{value: string, text: string}>;

  selectedSchedulingTerm: string;

  schedulingTerms: Array<string>;

  possibleSchedulingTerms: Array<{value: string, text: string}>;

  showClassSelector: boolean;

  showClasses: boolean;

  showGroups: boolean;

  links: Array<ClassPickerNavItem>;

  attendanceStatusLink: ClassPickerNavItem;
  // when true the ui to switch between classes or groups is gone so only classes can be selected.
  isClassOnly: boolean;

  ready(): void {
    this.showClasses = true;
    this.isClassOnly = false; //default to show classes or groups
    this.possibleSchedulingTerms = [];
    this.possibleYears = [];
    this.studentMap = new Map();
    this.selectedStudent = '';
    this.addEventListener('pdsClassChange', this.classChangeEvent);
    this.addEventListener('pdsCloseClassPicker', this.closeClassSelector);
  }

  messageKeysUpdated(): void {}

  toggleClassSelector(event: Event): void {
    event.preventDefault();
    this.showClassSelector = !this.showClassSelector;
    if (this.showClassSelector) {
      this.fire('pdsClassPickerOpen', true);
    }
  }

  closeClassSelector(): void {
    this.showClassSelector = false;
  }

  selectClasses(event: Event): void {
    event.preventDefault();
    this.showClasses = true;
    this.showGroups = false;
  }

  selectGroups(event: Event): void {
    event.preventDefault();
    this.showClasses = false;
    this.showGroups = true;
  }

  selectedClassDisplay(selectedClass: ClassPickerClass): string {
    if (selectedClass == null) {
      return 'No Class Selected';
    }
    if (selectedClass.schedulingTerm != null) {
      return `${selectedClass.periodDay} ${selectedClass.courseName}`;
    } else {
      if (selectedClass.sectionUids != null) {
        return `${selectedClass.groupName} - ${selectedClass.sectionUids.length} Classes`;
      } else {
        return `${selectedClass.groupName}`;
      }
    }
  }

  classChangeEvent(event: Event): void {
    let newClass: ClassPickerClass = (<Event & {detail: ClassPickerClass}>event).detail;
    this.selectedClass = newClass;
    this.showClassSelector = false;
  }

  yearChangeEvent(event: Event): void {
    this.fire('pdsYearChange', this.selectedYear);
  }

  schedulingTermChangeEvent(event: any): void {
    this.fire('pdsSchedulingTermChange', this.selectedSchedulingTerm);
  }

  studentChangeEvent(event: Event): void {
    this.fire('pdsStudentChange', this.selectedStudent);
  }

  classScheduleEmptyCheck(classSchedule: ClassSchedule): boolean {
    return classSchedule == null || classSchedule.sections == null || classSchedule.sections.length === 0;
  }

  classGroupingCheck(classGrouping: Array<ClassPickerClass>): boolean {
    return classGrouping != null && classGrouping.length > 0;
  }

  getPossibleYears(years: Array<string>): Array<{value: string, text: string}> {
    this.possibleYears.length = 0;
    for (let year of years) {
      this.possibleYears.push({
        text: year,
        value: year
      });
    }
    return this.possibleYears;
  }

  getPossibleSchedulingTerms(schedulingTerms: Array<string>): Array<{value: string, text: string}> {
    this.possibleSchedulingTerms.length = 0;
    for (let term of schedulingTerms) {
      this.possibleSchedulingTerms.push({
        text: term,
        value: term
      });
    }
    return this.possibleSchedulingTerms;
  }
  // returns a list of value text pairs for the dynamic options widget. student's uid is value, firstname+lastname is text
  getStudentOptions(studentOptions: ClassPickerStudent[]): Array<{value: string, text: string}> {
    let options: {value: string, text: string}[] = [];
    for (let student of studentOptions) {
      this.studentMap.set(student.uid, student);  // update student map in case students have changed.
      options.push({
        value: student.uid,
        text: `${student.firstName} ${student.lastName}`
      });
    }
    if (this.selectedStudent == null || this.selectedStudent.length === 0) {
      this.selectedStudent = studentOptions[0].uid; // if we have students but not selected student default to the first
    }
    return options;
  }

  // TODO: temporary function to assign a random-ish color to the class or group.
  getSectionColorClass(sectionOrGroup: any): string {
    return pdsUtils.getSectionColorForClass(sectionOrGroup);
  }

  hasAllSectionsGroup(classSchedule: ClassSchedule, isClassOnly: boolean): boolean {
    return isClassOnly && classSchedule.allClasses != null;
  }

  getInfoMessage(selectedStudent: string): string {
    if (this.studentOptions != null && this.studentOptions.length > 0) {
      let student = this.studentMap.get(selectedStudent);
      return `${student != null ? student.firstName : 'The selected student '} has no classes scheduled during the current term.`;
    } else {
      return 'There are no classes in your schedule during the selected year.';
    }
  }
}



