//NOTE: This widget's definition is included in powerSchoolDesignSystemToolkit.ts
import {PdsAbstractInputWidget} from './pds-abstract-input-widget';
import {BasicValidationRules} from './interfaces/basic-validation-rules';

export class PdsTextWidget extends PdsAbstractInputWidget {
  is = 'pds-text-widget';

  //Text Labels
  dataRegexErrorText: string;

  //Options
  dataInputType: string;

  //Validation Rules
  dataRegex: string;
  dataRegexmodifiers: string;
  dataMinlength: string;
  dataMaxlength: string;
  dataExcludechars: string;

  //Used to cache the regex so we don't keep re-creating it.
  validationRegex: RegExp;

  // these are deprecated
  dataAdditionalInputAttributes: Object;
  dataAdditionalErrorAttributes: Object;

  properties = {
    //AbstractInputWidget Properties
    name: String, //We construct the id using the name property
    dataLabelText: String,
    dataBadgeText: {
      type: String,
      value: '',
      observer: 'setUpBadge'
    },
    dataHelperText: {
      type: String,
      value: null
    },
    dataFieldHelpText: String,
    dataPlaceholderText: String,
    dataTooltipText: String,
    dataIsreadonly: {
      type: String,
      value: 'false',
      observer: 'setReadonlyText'
    },
    dataIsfieldonly: {
      //This is for adding a class to get rid of the bottom padding.
      type: String,
      value: 'false'
    },
    dataHideUnelevatedBadge: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    dataDisableInvalidBadgeElevation: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    //Widget specific Validation Properties
    dataRegexErrorText: String,
    dataInputType: {
      type: String,
      value: 'text'
    },
    dataIsrequired: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    dataMinlength: String,
    dataMaxlength: String,
    dataExcludechars: String,
    dataRegex: String,
    dataRegexmodifiers: String,
    dataCustomvalidate: String,
    //TODO: Make this look deprecated. We do NOT want this to be used by anyone
    //except for the secure project.
    dataAdditionalInputAttributes: Object,
    dataAdditionalErrorAttributes: Object
  };

  generateValidationRules(): void {
    let validationRules: BasicValidationRules = this.pds.validationEngine.getBasicValidationRules();

    validationRules.isText = true;
    validationRules.setProperty('isRequired', this.dataIsrequired, Boolean);
    validationRules.setProperty('maxLength', this.dataMaxlength, Number);
    validationRules.setProperty('minLength', this.dataMinlength, Number);
    validationRules.setProperty('excludeChars', this.dataExcludechars, String);

    //This is more complicated so we can't set it through setProperty.
    if (this.dataRegex != null && this.dataRegex.length > 0 &&
        this.dataRegexErrorText != null && this.dataRegexErrorText.length > 0) {
      validationRules.regex = {
        pattern: this.dataRegex != null ? new RegExp(this.dataRegex, this.dataRegexmodifiers) : new RegExp(this.dataRegex),
        errorMessage: this.dataRegexErrorText
      };
    }

    this.validators = this.pds.validationEngine.getValidatorsForRules(validationRules);

    if ('dataCustomvalidate' in this) {
      this.validators.push({
        execute: this.pds.validation.getCustomValidate(this.dataCustomvalidate)
      });
    }
  }

  /**
   * @param modelValue
   * @param skip
   * @returns returns if the model value was actually set.
   */
  setModelValue(modelValue: string, skip: boolean): boolean {
    let ele = this.querySelector('input');
    if (ele != null && modelValue !== ele.modelValue) {
      ele.modelValue = modelValue;
      if (skip !== true) {
        this.setValue(modelValue, true);
      }
      return true;
    }
    return false;
  }

  messageKeysUpdated(): void {
  }

  /**
   *
   * @param value - The string the user typed into the input.
   * @param skip - skips setting the model value. (Keeps from getting into recursive loops)
   */
  setValue(value: string, skip = false): void {
    if (value == null) {
      value = '';
    }

    let inputEl = this.querySelector('input');
    if (inputEl != null && value !== inputEl.value) {
      inputEl.value = value;
    }

    if (skip !== true) {
      this.setModelValue(value, true);
    }
  }

  //@Deprecated: Don't use this, but don't delete it either.
  setAdditionalInputAttributes(): void {
    if (this.dataAdditionalInputAttributes != null) {
      let inputElement = this.querySelector('input');
      for (let key in this.dataAdditionalInputAttributes) {
        if (this.dataAdditionalInputAttributes.hasOwnProperty(key)) {
          inputElement.setAttribute(key, this.dataAdditionalInputAttributes[key]);
        }
      }
    }
  }

  //@Deprecated: Don't use this, but don't delete it either.
  setAdditionalErrorAttributes(): void {
    if (this.dataAdditionalErrorAttributes != null) {
      let errorElement = this.querySelector('.pds-validation');
      for (let key in this.dataAdditionalErrorAttributes) {
        if (this.dataAdditionalErrorAttributes.hasOwnProperty(key)) {
          errorElement.setAttribute(key, this.dataAdditionalErrorAttributes[key]);
        }
      }
    }
  }
}



