import {PdsAbstractInputWidget} from './pds-abstract-input-widget';
import {BasicValidationRules} from './interfaces/basic-validation-rules';

export class PdsDateWidget extends PdsAbstractInputWidget {
  is = 'pds-date-widget';

  //Text Labels
  buttonText: string;

  //Validation Properties
  dataMaxdate: string;
  dataMindate: string;

  //Calendar specific Properties
  dialogValue: Date;
  showDatePickerDialog: boolean = false;

  properties = {
    //AbstractInputWidget Properties
    name: String, //We construct the id using the name property
    dataLabelText: String,
    dataBadgeText: {
      type: String,
      value: '',
      observer: 'setUpBadge'
    },
    dataHelperText: {
      type: String,
      value: null
    },
    dataFieldHelpText: String,
    dataPlaceholderText: String,
    dataTooltipText: String,
    dataIsreadonly: {
      type: String,
      value: 'false',
      observer: 'setReadonlyText'
    },
    dataIsfieldonly: {
      //This is for adding a class to get rid of the bottom padding.
      type: String,
      value: 'false'
    },
    dataHideUnelevatedBadge: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    dataDisableInvalidBadgeElevation: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    //Widget specific Validation Properties
    dataIsrequired: {
      type: String,
      value: 'false',
      observer: 'setUpBadge'
    },
    dataMaxdate: String,
    dataMindate: String,
    dataCustomvalidate: String
  };

  generateValidationRules(): void {
    let validationRules: BasicValidationRules = this.pds.validationEngine.getBasicValidationRules();

    validationRules.isDate = true;
    validationRules.setProperty('isRequired', this.dataIsrequired, Boolean);
    validationRules.setProperty('maxValue', this.dataMaxdate, Date);
    validationRules.setProperty('minValue', this.dataMindate, Date);

    this.validators = this.pds.validationEngine.getValidatorsForRules(validationRules);

    if ('dataCustomvalidate' in this) {
      this.validators.push({
        execute: this.pds.validation.getCustomValidate(this.dataCustomvalidate)
      });
    }
  }

  /**
   * @param modelValue
   * @param skip
   * @returns returns if the model value was actually set.
   */
  setModelValue(modelValue: Date, skip = false): boolean {
    let ele = this.querySelector('input');
    if (ele != null && modelValue !== ele.modelValue) {
      ele.modelValue = modelValue;
      this.dialogValue = modelValue;
      if (skip !== true) {
        this.setValue(this.pds.i18n.localizeDate(modelValue), true);
      }
      return true;
    }
    return false;
  };

  messageKeysUpdated(): void {
    this.buttonText = this.pds.i18n.getText(this.pds.i18n.widgetKeys['dateWidget.open_picker_dialog']);
  }

  setValue(value, skip = false): void {
    if (value == null) {
      value = '';
    }
    let inputEl = this.querySelector('input');
    if (inputEl != null && value !== inputEl.value) {
      inputEl.value = value;
    }
    if (skip !== true) {
      this.setModelValue(this.pds.i18n.deLocalizeDate(value), true);
    }
  }

  dateChanged(e): void {
    //Although we could directly set the modelValue, setting the value as the user would
    //and then calling inputChange goes through the same path that things go through when the user types in dates which
    //should be more maintanable.
    this.value = this.pds.i18n.localizeDate(e.target.value);
    this.inputChange();
    this.validateWidget(true);
    this.closeDatePickerDialog();
  };

  openDatePickerDialog(): void {
    this.showDatePickerDialog = true;
  };

  closeDatePickerDialog(): void {
    this.showDatePickerDialog = false;
  };
}



